s.formAction = getCoolServerUrl(this.publicWopiUrl)\n\t\t\tconsole.debug('Form action URL generated:', this.formAction)\n\t\t} else {\n\t\t\tconsole.error('wopiUrl prop is missing')\n\t\t}\n\t\tconsole.debug('Form action URL generated')\n\t\t// Submit the form to load the iframe content\n\t\tthis.$nextTick(() => {\n\t\t\tif (this.$refs.form) {\n\t\t\t\tthis.$refs.form.submit()\n\t\t\t} else {\n\t\t\t\tconsole.error('Form reference not found')\n\t\t\t}\n\t\t})\n\t},\n}\n</script>\n\n  <style scoped>\n  .cool-frame-iframe {\n    width: 100%;\n    border: none;\n\theight: 60vh;\n\toverflow-y: auto;\n  }\n  </style>\n"],"sourceRoot":""}]);
